:root {
  --bg_gradient: linear-gradient(to left top, var(--bg), var(--lgray));
  --bg_gradient_invert: linear-gradient(to right top, var(--lgray), var(--dBlue));
  --bg_gradient_card: linear-gradient(to left top, var(--blue), #87AECA);
  --bg: #0E1119;
  --blue: #478CBF;
  --dBlue: #23314D;
  --orange: #F27841;
  --gray: #36414C;
  --lgray: #616B80;
  --link_hover_color_lblue: #28C2E0;
  --radius: 10px;
  --button_radius: 7px;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
hr{
  width: 100%;
  margin: 0 auto;

}
