 .App{
  font-family: 'Oswald', sans-serif;
  font-size: 22px;
  height: 100vh;
  background: var(--bg);


}

.wrap {
  height: 83vh;




}

.header {
  font-family: 'Russo One', sans-serif;
  height: 8vh;
  background:var(--gray);
  margin: 0 auto 3%;
  padding: 0 12%;
  text-align: center;
}

.logo-section {
  float: left;

}

.logotype {
  color: #478CBF;
  font-family: 'Russo One', sans-serif;
  font-size: 3vw;
  margin: 2% auto 0;
  animation: bright 1s linear 3;
}

@keyframes bright {
  50% {
      filter: brightness(185%);
  }
}

.menu-section {
  float: right;


}

.menu {
  margin: 5% auto 0;

}
.menu> a{
  margin: 0 1vw;
}

.pop-menu {

  display: none;
  float: none;
  background-color: var(--lgray);
  font-size: 12vw;
 
  z-index: 4;
  position: absolute;
  left: 25vw;
  border-radius: 4%;


}

.menu a,
.pop-menu a {
  text-decoration: none;
  color: aqua;

}

.wrapped-menu {
  color: #478CBF;
  font-size: 5vw;
  display: none;

}

.main-content {
/* background-color: #F27841; */
  height: 75vh;
  width: 100%;
  padding: 2% 0;
  /* float: left; */
  margin: 0 auto;

}
.cards-section {

  overflow: auto;
  margin: 0 auto;
  width: 90%;
  height: 95%;
  padding-right: 12%;
}



